/* stylelint-enable */
html,
body {
  min-width: 320px; }

body {
  padding: 0;
  margin: 0 auto;
  width: auto;
  font-family: "Quicksand", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: black; }

@media (min-width: 768px) {
  body {
    width: auto; } }

@media (min-width: 1300px) {
  body {
    width: auto; } }

.page-header {
  position: relative;
  width: 100%;
  min-height: 53px;
  border-bottom: 1px solid #707070; }

.page-header__logo img {
  position: absolute;
  top: 7px;
  left: 44%;
  width: 44px;
  height: 35px; }

.page-header__search img {
  position: absolute;
  top: 3px;
  left: 12%; }

.page-header__favorites img {
  position: absolute;
  top: 6px;
  left: 79%; }

.page-header__basket img {
  position: absolute;
  top: 6px;
  left: 89%; }

.page-header__ticker {
  display: none; }

.page-header__btn-menu {
  padding: 5px;
  position: absolute;
  top: 8px;
  left: 12px;
  cursor: pointer;
  transition: right .23s;
  z-index: 3;
  width: 20px;
  transform: translateZ(0);
  backface-visibility: hidden; }

.page-header__btn-menu span {
  display: block;
  height: 2px;
  background-color: #444;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative; }

.page-header__ticker:checked ~ .page-header__btn-menu {
  left: 220px; }

.page-header__ticker:checked ~ .main-nav {
  left: 0; }

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__first {
  transform: rotate(45deg); }

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__second {
  opacity: 0;
  display: none; }

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__third {
  transform: rotate(-45deg);
  top: -7px; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main-nav {
  display: block;
  position: fixed;
  list-style: none;
  padding-top: 10px;
  margin: 0;
  box-sizing: border-box;
  width: 250px;
  background-color: white;
  box-shadow: 8px 10px 12px -4px #f5f5f5;
  height: 100%;
  top: 0;
  left: -250px;
  transition: left .3s;
  z-index: 2;
  transform: translateZ(0);
  backface-visibility: hidden; }

.main-nav__city {
  display: flex;
  margin-left: 15px;
  font-size: 10px; }

.main-nav__city img {
  margin-right: 5px; }

.main-nav__city-text {
  margin: 0;
  margin-top: -2px; }

.main-nav__list {
  margin: 0;
  padding: 0;
  margin-top: 8px;
  padding-bottom: 5px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  border-bottom: 1px solid #707070; }

.main-nav__item {
  list-style: none; }

.main-nav__item a {
  text-decoration: none;
  color: #70707070; }

.main-nav__item--active a {
  color: black;
  padding-bottom: 5px;
  border-bottom: 3px solid black; }

.main-menu__list {
  margin: 0;
  padding: 0;
  padding-top: 15px;
  padding-left: 15px; }

.main-menu__item {
  padding-bottom: 5px;
  list-style: none; }

.main-menu__item a {
  text-decoration: none;
  color: black; }
