.main-nav {
  display: block;
  position: fixed;
  list-style:none;
  padding-top: 10px;
  margin: 0;
  box-sizing: border-box;
  width: 250px;
  background-color: white;
  box-shadow: 8px 10px 12px -4px #f5f5f5;
  height: 100%;
  top: 0;
  left: -250px;
  transition: left .3s;
  z-index: 2;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.main-nav__city {
  display: flex;
  margin-left: 15px;
  font-size: 10px;
}

.main-nav__city img {
  margin-right: 5px;
}

.main-nav__city-text {
  margin: 0;
  margin-top: -2px;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  margin-top: 8px;
  padding-bottom: 5px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  border-bottom: 1px solid #707070;
}

.main-nav__item {
  list-style: none;
}

.main-nav__item a {
  text-decoration: none;
  color: #70707070;
}

.main-nav__item--active a {
  color: black;
  padding-bottom: 5px;
  border-bottom: 3px solid black;
}
